export default {
  methods: {
    // 
    convertStatus(status) {
      if (!status) return;
      let word = status.toUpperCase();
      switch (word) {
        case "WAIT":
        case "WAITAPP":
        case "NEW":
          return "";
        case "APPROVE":
          return "";
        case "CLOSE":
        case "DONE":
          return "";
        case "DRAFT":
          return "";
        case "REJECT":
          return "";
      }
    },
    // 
    getEffect(effect) {
      return effect === "0" ? "" : "";
    },
    // 
    getCourseState(status) {
      if (!status) return;
      let word = status.toUpperCase();
      switch (word) {
        case "OPEN":
          return "";
        case "STOP":
          return "";
        case "FULL":
          return "";
        default:
          return "";
      }
    },
    // 
    getApproveStatus(status) {
      switch (status) {
        case "0":
          return "";
        case "1":
          return "";
        case "2":
          return "";
      }
    },
    // 
    getAttendStatus(status) {
      switch (status) {
        case "0":
          return "";
        case "1":
          return "";
        case "2":
          return "";
      }
    },
    // 
    getIssueCardStatus(status) {
      switch (status) {
        case "0":
          return "";
        case "1":
          return "";
        default:
          return "";
      }
    },
    // 
    signUpStatus(status, approveStatus) {
      const newDate = this.formatDate(new Date());
      if (status === newDate) {
        return "";
      } else if (status > newDate && approveStatus === "0") {
        return "";
      } else if (status > newDate && approveStatus === "1") {
        return "";
      } else if (status < newDate && approveStatus !== "2") {
        return "";
      } else if (approveStatus === "2") {
        return "";
      }
    },
    // yyyy/MM/dd
    formatDate(date) {
      let d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("/");
    },
    // file base64
    toBase64(file) {
      return new Promise((resolve, reject) => {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = (error) => reject(error);
      });
    },
    // (yyyy/mm/dd)
    getDay() {
      let full = new Date();
      return full.toLocaleDateString();
    },
    /**
     * res API 
     * file_Name 
     * file_Format 
     */
    downloadFile(res, file_Name, file_Format) {
      const href = URL.createObjectURL(res.data);
      const link = document.createElement("a");
      link.href = href;
      // file_Name  file_Format
      if (file_Name.includes(".")) {
        link.setAttribute("download", `${file_Name}`);
      } else {
        link.setAttribute("download", `${file_Name}.${file_Format}`);
      }
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(href);
    },
    // value key
    getURLNewParams(obj) {
      let qryString = [];
      let params = "";
      Object.keys(obj).forEach((e) => {
        if (obj[e]) {
          qryString.push(encodeURI(`${e}=${obj[e]}`));
        }
      });
      params = qryString.join("&");
      return params;
    },
    /**
     * FormData
     */
    changeFormData(data) {
      const formData = new FormData();
      Object.entries(data).forEach(([key, value]) => {
        if (value) {
          if (Array.isArray(value)) {
            value.forEach((item) => {
              formData.append(key, item);
            });
          } else {
            formData.append(key, value);
          }
        }
      });
      return formData;
    },
    /**
     *   ()
     * @param {} fn
     * @param {(),t,500} t
     * @param {t(),timeout2contExecuteFun,true} timeout2contExecuteFun
     */
    throttle(fn, t, timeout2contExecuteFun = true) {
      let last;
      let timer;
      let interval = t || 500;
      return function () {
        let args = arguments;
        let now = +new Date();
        if (last && now - last < interval) {
          clearTimeout(timer);
          timer = setTimeout(() => {
            last = now;
            if (timeout2contExecuteFun) fn.apply(this, args);
          }, interval);
        } else {
          last = now;
          fn.apply(this, args);
        }
      };
    },
    /**
     *  ()
     * @param {} fn
     * @param {(),t,500} t
     */
    debounced(fn, t) {
      let delay = t || 500;
      let timer;
      return function () {
        let args = arguments;
        if (timer) {
          clearTimeout(timer);
        }
        timer = setTimeout(() => {
          timer = null;
          fn.apply(this, args);
        }, delay);
      };
    },
  },
};
