const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
});

/**
 * F5API refresh token
 * to.name === index, 404, 999 API
 * Vuex.state.loginSidebarsidebar API
 */
router.beforeEach(async (to, from, next) => {
  //let newUrl = new URL(window.location.href.replace(/#\//g, "?"));
  //console.log(newUrl);
  if (to.name === "index" || to.name === "404" || to.name === "999") {
    next();
  } else {
    const sidebar = Vuex.state.loginSidebar;
    Vuex.state.currentPage = 1;
    if (sidebar.length) {
      next();
    } else {
      if (localStorage.getItem("ah")) {
        await Vuex.dispatch("qrySideBar");
        next();
      } else {
        if (
          to.name === "forgetPswd" ||
          to.name === "companyApplyAccountStep1" ||
          to.name === "companyApplyAccountStep2"
        ) {
          next();
          return;
        }
        if (to.name !== "index" && !self.redirectUrl) {
          self.redirectUrl = to.name;
        }
        next({ name: "index" });
      }
    }
  }
});
router.afterEach((to) => {
  if (self.redirectUrl && to.name === "frontPage") {
    router.replace({ name: self.redirectUrl });
    self.redirectUrl = null;
  }
});
export default router;
